.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.section ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

main {
  width: 70%;
  margin: 0 auto;
}

@media screen and (max-width: 600px) {
  main {
    width: 100%;
  }
}

.question {
  border: 1px solid black;
  margin: 15px 10px;
  padding: 15px;
}

.question-text {
  font-weight: bold;
}

.show-hint {
  font-size: 1.1em;
  border: 2px solid black;
  background-color: white;
  padding: 5px 15px;
}

.question-hint {
  color: red;
  font-weight: bold;
}

